<template>
<div class="selfItem">
    <div class="selfItemTop">
        <div class="imgShow"><img class="showImg" :src="info.userpic"></div>
        <label class="myname">{{info.username}}</label>
        <label class="mytime">{{this.time}}</label>
    </div>
    <div class="selfItemMid">
        {{info.content}}
        <div class="contentImg">
            <!-- <div v-for="(item,index) in info.picList" :key="index" class="subContentImg">
                 <img class="showImg"  :src="info.picList[index]">
            </div> -->
            <viewer :images="images">
                <div v-for="(item,index) in info.picList" :key="index" class="subContentImg">
                    <img class="showImg" :src="item" :key="item">
                </div>
            </viewer>  
        </div>
        
    </div>


</div>
    
</template>
<script>
export default {
    props:['info'],
    data(){
        return{
            images:this.info.picList,
        }
    },
    computed:{
        time(){
            var d =new Date();
            d.setFullYear(1999);
            var date = new Date();
            var timeGo =(date.getTime()-this.info.time)/1000;
            if(timeGo <60){
                return '刚刚'
            } else if(timeGo <3600){
                return Math.floor(timeGo/60)+'分钟前';
            } else if(timeGo < 3600*24){
                return Math.floor(timeGo/3600)+'小时前'
            } else {
                var thatTime = new Date(this.info.time);
                return thatTime.getFullYear()+'-'+thatTime.getMonth()+'-'+thatTime.getDate()+'   '+thatTime.getHours()+':'+thatTime.getMinutes(); 
            }
        }

    },
    mounted(){
        // var img = document.getElementById("myimg");
        // if(img.naturalWidth > img.naturalHeight){
        //     img.style = 'height:100%;width:auto';
        // } else {
        //      img.style = 'width:100%;height:auto';
        // }
        var imgs = document.getElementsByClassName("showImg");
        for(var i = 0;i < imgs.length;i++){
            if(imgs[i].naturalWidth > imgs[i].naturalHeight){
                imgs[i].style = 'width:auto;height:100%';
            } else {
                imgs[i].style = 'width:100%;height:auto';
            }

        }
        
    }

    
}
</script>
<style>
.selfItem{
    position: relative;
    border:black 0.4vw solid;
    margin-bottom:5vw;

    border-radius: 4vw;
    padding:2vw;
    word-wrap:break-word;
}
.imgShow{
    position:absolute;
    height: 13vw;
    width: 13vw;
    border-radius: 2vw;
    overflow: hidden;
}
.imgShow img{
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    margin:auto auto;
}
.myname{
    position:absolute;
    color:#247fa7;
    font-size: 6vw;
    font-weight:bold;
    left:17vw;
}
.mytime{
    position:absolute;
    top:10vw;
    left:17vw;
    font-size: 4.5vw;
    color:rgb(124, 117, 117);
}
.selfItemTop{
    height: 15vw;
    border-bottom: #CCC 0.2vw solid;
}
.selfItemMid{
    font-size: 5.5vw;
    position: relative;
    font-weight: 530;
}
.contentImg{
    line-height: 0;
    margin-top:5vw;
}
.subContentImg{
    position: relative;
    height: 30vw;
    width: 30vw;
    overflow: hidden;
    display: inline-block;
    margin:0.5vw;
}
.showImg{
    position: absolute;
    left:0;
    right: 0;
    top:0;
    bottom:0;
    margin:auto auto;

}
</style>





