<template>
<div>
    <router-link to="/"><img src="../../../static/img/back_bl.png"></router-link>
    <div class="selfTop">
        <div class="show"> <img id="myimg"  :src="this.global.info.userpic"></div>
        <label>{{this.global.info.username}}</label>
    </div>
    <div class="selfMid">
        <mt-field label="姓名" :state="errorList['name'].state" @input="checkName" placeholder="老王" v-model="username"></mt-field>
        <div class="error" v-show="errorList['name'].state === 'error'"><label><img src="../../../static/img/error.png">{{this.errorList['name'].error}}</label></div>
         <mt-field label="手机号" :state="errorList['phone'].state" @input="checkPhone" placeholder="13XXXXXXXXX" v-model="userphone"></mt-field>
        <div class="error" v-show="errorList['phone'].state === 'error'"><label><img src="../../../static/img/error.png">{{this.errorList['phone'].error}}</label></div>
        <mt-field label="地址"  placeholder="XX宿舍楼XX" v-model="useraddress"></mt-field>
        <div class="selfintro">
            <mt-field class="myintr" label="自我介绍" placeholder="自我介绍" type="textarea" rows="1" v-model="userselfintro"></mt-field>
        </div>
        
    </div>
    <div class="selfBottom">
        <button>保存提交</button>
    </div>
</div>
    
</template>
<script>
import { Field } from 'mint-ui';
export default {
    data(){
        return {
            username:this.global.info.username,
            userphone:this.global.info.userphone,
            useraddress:this.global.info.useraddress,
            userselfintro:this.global.info.userselfintro,
            errorList:{
                name:{
                    state:'none',
                    error:''

                },
                phone:{
                    state:'none',
                    error:''

                }
            }
        }
    },
    methods:{
        checkName(){
            if(this.username === ''){
                this.errorList['name'].state = 'error';
                this.errorList['name'].error = '姓名不能为空';
                return false;
            }
            if(!/^[\w_\u4e00-\u9fa5]+$/.test(this.username)){
                this.errorList['name'].state = 'error';
                this.errorList['name'].error = '姓名包含非法字符';
                return false;
            }
            if(!/^[\w_\u4e00-\u9fa5]{1,30}$/.test(this.username)){
                this.errorList['name'].state = 'error';
                this.errorList['name'].error = '姓名过长';
                return false;
            }
            this.errorList['name'].state = 'success';
            return true;
        },
        checkPhone(){
             if(this.userphone === ''){
                this.errorList['phone'].state = 'error';
                this.errorList['phone'].error = '电话不能为空';
                return false;
            }
            if(!/^0?(13|15|18)[0-9]{9}$/.test(this.userphone)){
                this.errorList['phone'].state = 'error';
                this.errorList['phone'].error = '请输入正确的电话号码';
                return false;
            }
            this.errorList['phone'].state = 'success';
            return true;
        },
    },
    mounted(){
        var img = document.getElementById("myimg");
        if(img.naturalWidth > img.naturalHeight){
            img.style = 'height:100%;width:auto';
        } else {
             img.style = 'width:100%;height:auto';

        }


    },
    computed:{
    }
    
    
}
</script>
<style>
.selfTop{
    position: relative;
    margin-top:10vw;
    height: 55vw;
    border-bottom:#169bd5 0.6vw dashed
}
.show{
    position:absolute;
    left: 0;
    right: 0;
    margin:0 auto;
    height: 35vw;
    width:35vw;
    border:black solid 1vw;
    border-radius: 50%;
    overflow: hidden;
    text-align: center;
    line-height: 30vw;
}
.show img{
    position:absolute;
    left:0;
    top:0;
    bottom:0;
    right: 0;
    margin:auto auto;
}
.selfTop label{
    position:absolute;
    text-align: center;
    font-size: 10vw;
    width:100%;
    top:38vw;
}
.error{
    margin-top:0.8vw;
    color:red;
    text-align: right;
    padding-right: 5vw;
    height: 9vw;
    line-height: 9vw;
    background: white;
}
.error img{
    height: 5vw;
    width: 5vw;
    margin-right: 1vw;

}
.error label{
    border:red 0.3vw solid;
    padding:1vw;
    border-radius: 2vw;
}
.selfMid{
    position: relative;
}
.selfBottom{
    position: relative;
    margin-top:10vw;
}
.selfBottom button{
    outline: 0;
    position:absolute;
    border-radius: 2vw;
    height: 14vw;
    width: 77.8vw;
    border:0;
    background:#169bd5;
    color: white;
    text-align: center;
    line-height: 14vw;
    left: 0;
    right: 0;
    margin:auto auto;

}
a img{
    margin-top:3vw;
    margin-left: 2vw;
}


</style>




